"use client"

import { useEffect, useState } from "react"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Search, ChevronLeft, ChevronRight, Eye } from "lucide-react"
import { useStudentStore } from "@/store/student.store"
import { StudentDetailsModal } from "@/components/Modal/student-details-modal"

export function AllStudents() {
  const { students, loading, page, limit, total, search, fetchStudents, setPage, setSearch } =
    useStudentStore()

  const [selectedStudent, setSelectedStudent] = useState<any>(null)
  const [open, setOpen] = useState(false)

  useEffect(() => {
    fetchStudents()
  }, [page, search])

  const totalPages = Math.ceil(total / limit)

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold">All Students</h1>
        <p className="text-muted-foreground">View and manage student records</p>
      </div>

      {/* Search */}
      <Card>
        <CardContent className="p-4">
          <div className="relative max-w-sm">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
            <Input
              placeholder="Search by admission no or name"
              className="pl-10"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>
        </CardContent>
      </Card>

      {/* Table */}
      <Card>
        <CardHeader>
          <CardTitle>Student Records</CardTitle>
          <CardDescription>
            Showing {students.length} of {total} students
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="rounded-lg border overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Admission No</TableHead>
                  <TableHead>Name</TableHead>
                  <TableHead>Father</TableHead>
                  <TableHead>Mother</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Actions</TableHead>
                </TableRow>
              </TableHeader>

              <TableBody>
                {loading && (
                  <TableRow>
                    <TableCell colSpan={6} className="text-center">
                      Loading...
                    </TableCell>
                  </TableRow>
                )}

                {!loading && students.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={6} className="text-center">
                      No students found
                    </TableCell>
                  </TableRow>
                )}

                {students.map((student) => (
                  <TableRow key={student.id}>
                    <TableCell className="font-mono">{student.AdmissionNo}</TableCell>
                    <TableCell className="font-medium">{student.student_name}</TableCell>
                    <TableCell>{student.ParentNameFather || "-"}</TableCell>
                    <TableCell>{student.Mother || "-"}</TableCell>
                    <TableCell>
                      <Badge variant="secondary">{student.status || "N/A"}</Badge>
                    </TableCell>
                    <TableCell className="text-right">
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => {
                          setSelectedStudent(student)
                          setOpen(true)
                        }}
                      >
                        <Eye className="w-4 h-4" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          {/* Pagination */}
          <div className="flex items-center justify-between mt-4">
            <p className="text-sm text-muted-foreground">
              Page {page} of {totalPages}
            </p>
            <div className="flex gap-2">
              <Button
                variant="outline"
                size="sm"
                disabled={page === 1}
                onClick={() => setPage(page - 1)}
              >
                <ChevronLeft className="w-4 h-4 mr-1" />
                Previous
              </Button>

              <Button
                variant="outline"
                size="sm"
                disabled={page === totalPages}
                onClick={() => setPage(page + 1)}
              >
                Next
                <ChevronRight className="w-4 h-4 ml-1" />
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Student Details Modal */}
      <StudentDetailsModal
        open={open}
        onClose={() => setOpen(false)}
        student={selectedStudent}
      />
    </div>
  )
}
export default AllStudents