"use client";

import { create } from "zustand";
import { Student, fetchStudents, addStudent, updateStudent, deleteStudent, deleteDuplicate } from "@/services/student.service";

interface StudentState {
  students: Student[];
  pagination: { total: number; page: number; limit: number };
  loading: boolean;
  fetch: (page?: number, limit?: number) => Promise<void>;
  add: (student: Partial<Student>) => Promise<void>;
  update: (student: Partial<Student>) => Promise<void>;
  remove: (student: Student) => Promise<void>;
}

export const useStudentStore = create<StudentState>((set, get) => ({
  students: [],
  pagination: { total: 0, page: 1, limit: 10 },
  loading: false,

  fetch: async (page = 1, limit = 10) => {
    const token = localStorage.getItem("auth_token");
    if (!token) throw new Error("Unauthorized");
    set({ loading: true });
    const data = await fetchStudents(page, limit, token);
    set({
      students: data.data,
      pagination: data.pagination,
      loading: false,
    });
  },

  add: async (student) => {
    const token = localStorage.getItem("auth_token");
    if (!token) throw new Error("Unauthorized");
    set({ loading: true });
    await addStudent(student, token);
    await get().fetch(get().pagination.page, get().pagination.limit);
    set({ loading: false });
  },

  update: async (student) => {
    const token = localStorage.getItem("auth_token");
    if (!token) throw new Error("Unauthorized");
    set({ loading: true });
    await updateStudent(student, token);
    await get().fetch(get().pagination.page, get().pagination.limit);
    set({ loading: false });
  },

  remove: async (student) => {
    const token = localStorage.getItem("auth_token");
    if (!token) throw new Error("Unauthorized");
    set({ loading: true });
    if (student.status === "duplicate") {
      await deleteDuplicate(student.AdmissionNo, token);
    } else {
      await deleteStudent(student.AdmissionNo, token);
    }
    await get().fetch(get().pagination.page, get().pagination.limit);
    set({ loading: false });
  },
}));
