const BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL!;
const API_KEY = process.env.NEXT_PUBLIC_API_KEY!;

export interface Student {
  id: number;
  AdmissionNo: string;
  student_name: string;
  ParentNameFather: string;
  Mother: string;
  address: string;
  OccupationFather: string;
  SchoolClass: string;
  TCNo: string;
  AdmissionDate: string;
  DateOfBirth: string;
  Nationlty: string;
  Relegion: string;
  Caste: string;
  MotherToung: string;
  ClassStudentAdmitted: string;
  LeavingClass?: string;
  DateOfLeaving?: string;
  ReasonOfLeaving?: string;
  NoAndDateTransferCertificate?: string;
  AdharNo: string;
  mobile: string;
  email: string;
  status: string;
  EntryDate: string;
}

// Fetch student list
export async function fetchStudents(page = 1, limit = 10, token: string) {
  const res = await fetch(
    `${BASE_URL}/student_list?page=${page}&limit=${limit}`,
    {
      headers: {
        "Authorization": `Bearer ${token}`,
        "X-API-Key": API_KEY,
      },
    }
  );
  const data = await res.json();
  if (!data.status) throw new Error(data.message || "Failed to fetch students");
  return data;
}

// Add new student
export async function addStudent(student: Partial<Student>, token: string) {
  const res = await fetch(`${BASE_URL}/student_insert`, {
    method: "POST",
    headers: {
      "Authorization": `Bearer ${token}`,
      "X-API-Key": API_KEY,
      "Content-Type": "application/json",
    },
    body: JSON.stringify(student),
  });
  const data = await res.json();
  if (!data.status) throw new Error(data.message || "Failed to add student");
  return data;
}

// Update student
export async function updateStudent(student: Partial<Student>, token: string) {
  const res = await fetch(`${BASE_URL}/student_update`, {
    method: "POST",
    headers: {
      "Authorization": `Bearer ${token}`,
      "X-API-Key": API_KEY,
      "Content-Type": "application/json",
    },
    body: JSON.stringify(student),
  });
  const data = await res.json();
  if (!data.status) throw new Error(data.message || "Failed to update student");
  return data;
}

// Delete main student
export async function deleteStudent(admissionNo: string, token: string) {
  const res = await fetch(`${BASE_URL}/delete_student.php`, {
    method: "POST",
    headers: {
      "Authorization": `Bearer ${token}`,
      "X-API-Key": API_KEY,
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ AdmissionNo: admissionNo, confirm: "DELETE" }),
  });
  const data = await res.json();
  if (!data.status) throw new Error(data.message || "Failed to delete student");
  return data;
}

// Delete duplicate student
export async function deleteDuplicate(admissionNo: string, token: string) {
  const res = await fetch(`${BASE_URL}/delete_duplicates_by_admission`, {
    method: "POST",
    headers: {
      "Authorization": `Bearer ${token}`,
      "X-API-Key": API_KEY,
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ AdmissionNo: admissionNo }),
  });
  const data = await res.json();
  if (!data.status) throw new Error(data.message || "Failed to delete duplicate");
  return data;
}
